// src/pages/Member/RegisterPage.jsx
import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";
import DatePicker from "react-datepicker";
import Breadcrumbs from "../../components/Breadcrumbs";
import "react-datepicker/dist/react-datepicker.css";

function RegisterPage() {
  const [formData, setFormData] = useState({
    member_id: "",
    passwd: "",
    name: "",
    birth: null,
    sex: "",
    hp1: "010",
    hp2: "",
    hp3: "",
    email: "",
    idChecked: false,
  });

  const [idCheckResult, setIdCheckResult] = useState({
    checked: false,
    message: "",
    status: "", // "ok", "dup", "no"  
  });

  const [error, setError] = useState(""); //    
  const navigate = useNavigate();

  // // axios   -     
  // useEffect(() => {
  //   axios.defaults.withCredentials = true;
  // }, []);

  //       
  useEffect(() => {
    const idInput = document.querySelector('input[name="member_id"]');
    if (idInput) {
      idInput.focus();
    }
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;

    //       
    if (name === "member_id") {
      setIdCheckResult({
        checked: false,
        message: "",
        status: "",
      });
      setFormData((prev) => ({
        ...prev,
        idChecked: false, //       
        [name]: value,
      }));
    } else {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  const handleDateChange = (date) => {
    setFormData((prev) => ({
      ...prev,
      birth: date,
    }));
  };

  //    
  const handleNumberInput = (e) => {
    const { value } = e.target;
    const onlyNums = value.replace(/[^0-9]/g, "");
    if (onlyNums !== value) {
      e.target.value = onlyNums;
    }
  };

  //    
  const handleFocusMove = (e, max, nextField) => {
    if (e.target.value.length >= max) {
      const nextInput = document.querySelector(`input[name="${nextField}"]`);
      if (nextInput) {
        nextInput.focus();
      }
    }
  };

  //   
  const handleIdCheck = async () => {
    const id = formData.member_id.trim();
    setError(""); //   

    if (!id) {
      setError("  .");
      return;
    }

    //    
    const abc = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
    if (abc.indexOf(id.charAt(0)) === -1) {
      setError("   .");
      setIdCheckResult({
        checked: false,
        message: "",
        status: "",
      });
      return;
    }

    //  
    if (id.length < 4 || id.length > 10) {
      setError("  4 10 .");
      setIdCheckResult({
        checked: false,
        message: "",
        status: "",
      });
      return;
    }

    try {
      const response = await axios.get(`/api/Member/check-id/${id}`);

      if (response.data.success) {
        if (response.data.available) {
          //   
          setIdCheckResult({
            checked: true,
            message: " .",
            status: "ok",
          });
          setFormData((prev) => ({ ...prev, idChecked: true }));
        } else {
          //   
          setIdCheckResult({
            checked: true,
            message: response.data.message || "   .",
            status: "dup",
          });
          setFormData((prev) => ({ ...prev, idChecked: false }));
        }
      } else {
        // API success: false  
        setIdCheckResult({
          checked: true,
          message: response.data.message || "  .",
          status: "no",
        });
        setFormData((prev) => ({ ...prev, idChecked: false }));
      }
    } catch (error) {
      console.error("   :", error);

      if (error.response && error.response.status === 409) {
        // () 
        setIdCheckResult({
          checked: true,
          message: "   .",
          status: "dup",
        });
        setFormData((prev) => ({ ...prev, idChecked: false }));
      } else {
        setError("     .");
        setIdCheckResult({
          checked: false,
          message: "",
          status: "",
        });
      }
    }
  };

  const handleRegister = async () => {
    setError(""); //   

    //  
    if (!formData.idChecked) {
      setError("   .");
      return;
    }
    if (!formData.member_id) {
      setError("  .");
      return;
    }
    if (!formData.passwd) {
      setError("  .");
      return;
    }
    if (!formData.name) {
      setError("  .");
      return;
    }
    if (!formData.birth) {
      setError("   .");
      return;
    }
    if (!formData.hp2) {
      setError("  .");
      return;
    }
    if (!formData.hp3) {
      setError("  .");
      return;
    }
    if (!formData.email) {
      setError("  .");
      return;
    }

    try {
      // API      -   
      const registerData = {
        //  RegisterRequest   Member_id 
        Member_id: formData.member_id,
        Passwd: formData.passwd,
        Name: formData.name,
        //    
        Birth: formData.birth ? formData.birth.toISOString().split("T")[0] : "",
        Sex: formData.sex,
        Hp1: formData.hp1,
        Hp2: formData.hp2,
        Hp3: formData.hp3,
        Email: formData.email,
        Zipcode: "", //  
        Address1: "", //  
        Address2: "", //  
      };

      const response = await axios.post("/api/Member/register", registerData);

      if (response.data.success) {
        alert(" .");
        navigate("/Member/Login");
      } else {
        setError(response.data.message || " .");
      }
    } catch (error) {
      console.error(" :", error);

      if (error.response) {
        //   
        if (error.response.status === 409) {
          setError("  .");
        } else if (
          error.response.data.errors &&
          Array.isArray(error.response.data.errors)
        ) {
          //   errors   
          setError(error.response.data.errors.join(", "));
        } else {
          setError(
            error.response.data.message || "    ."
          );
        }
      } else if (error.request) {
        //      
        setError("   .   .");
      } else {
        //     
        setError("   .");
      }
    }
  };

  return (
    <>
      {/* ======= Breadcrumbs ======= */}
      <Breadcrumbs
        title=""
        description="Est dolorum ut non facere possimus quibusdam eligendi voluptatem. 
        Quia id aut similique quia voluptas sit quaerat debitis. 
        Rerum omnis ipsam aperiam consequatur laboriosam nemo harum praesentium."
      />
      {/* ======= End Breadcrumbs ======= */}
      <section className="section section-shaped section-lg">
        <div className="container pt-lg-7">
          <div className="row justify-content-center">
            <div className="col-lg-5">
              <div className="card bg-secondary shadow border-0">
                <div className="card-header bg-white pb-5">
                  <div className="text-muted text-center mt-3">
                    <h2></h2>
                  </div>
                </div>
                <div
                  className="card-body px-lg-5 py-lg-5"
                  style={{ background: "#c1c1c1" }}
                >
                  <div className="text-center text-muted mb-4">
                    <small>   .</small>
                  </div>

                  {/*    */}
                  {error && (
                    <div className="alert alert-danger" role="alert">
                      {error}
                    </div>
                  )}

                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        name="member_id"
                        placeholder=""
                        className="form-control"
                        value={formData.member_id}
                        onChange={handleChange}
                      />
                      <button
                        type="button"
                        onClick={handleIdCheck}
                        className="btn btn-primary"
                        style={{
                          background: "#5fcf80",
                          borderColor: "#5fcf80",
                        }}
                      >
                        
                      </button>
                    </div>
                    {idCheckResult.checked && (
                      <span
                        id="sp_dupID"
                        style={{
                          color:
                            idCheckResult.status === "ok" ? "green" : "red",
                          fontWeight: "bold",
                        }}
                      >
                        {idCheckResult.message}
                      </span>
                    )}
                    <p>
                        ,   4 10
                    </p>
                  </div>

                  <div className="form-group focused">
                    <div className="input-group input-group-alternative">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-lock-circle-open"></i>
                        </span>
                      </div>
                      <input
                        type="password"
                        name="passwd"
                        placeholder=""
                        className="form-control"
                        value={formData.passwd}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  {/*      */}
                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-hat-3"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        name="name"
                        placeholder=""
                        className="form-control"
                        value={formData.name}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <DatePicker
                        selected={formData.birth}
                        onChange={handleDateChange}
                        dateFormat="yyyy-MM-dd"
                        placeholderText=" "
                        className="form-control"
                        name="birth"
                        readOnly
                      />
                    </div>
                  </div>

                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <input
                        className="form-check-input"
                        type="radio"
                        name="sex"
                        id="sex1"
                        value=""
                        checked={formData.sex === ""}
                        onChange={handleChange}
                        style={{ marginLeft: "10px" }}
                      />
                      <label
                        className="form-check-label"
                        htmlFor="sex1"
                        style={{ marginLeft: "10px" }}
                      >
                        
                      </label>
                      <input
                        className="form-check-input"
                        type="radio"
                        name="sex"
                        id="sex2"
                        value=""
                        checked={formData.sex === ""}
                        onChange={handleChange}
                        style={{ marginLeft: "10px" }}
                      />
                      <label
                        className="form-check-label"
                        htmlFor="sex2"
                        style={{ marginLeft: "10px" }}
                      >
                        
                      </label>
                    </div>
                  </div>

                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        name="hp1"
                        value={formData.hp1}
                        onChange={handleChange}
                        className="form-control"
                        placeholder=""
                        onKeyUp={(e) => {
                          handleNumberInput(e);
                          handleFocusMove(e, 3, "hp2");
                        }}
                        maxLength={3}
                      />
                      -
                      <input
                        type="text"
                        name="hp2"
                        value={formData.hp2}
                        onChange={handleChange}
                        className="form-control"
                        placeholder=""
                        onKeyUp={(e) => {
                          handleNumberInput(e);
                          handleFocusMove(e, 4, "hp3");
                        }}
                        maxLength={4}
                      />
                      -
                      <input
                        type="text"
                        name="hp3"
                        value={formData.hp3}
                        onChange={handleChange}
                        className="form-control"
                        placeholder=""
                        onKeyUp={(e) => {
                          handleNumberInput(e);
                          handleFocusMove(e, 4, "email");
                        }}
                        maxLength={4}
                      />
                    </div>
                  </div>

                  <div className="form-group">
                    <div className="input-group input-group-alternative mb-3">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <input
                        type="email"
                        name="email"
                        placeholder=""
                        className="form-control"
                        value={formData.email}
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="text-center">
                    <button
                      type="button"
                      onClick={handleRegister}
                      className="btn btn-success mt-4"
                      style={{ background: "#5fcf80", borderColor: "#5fcf80" }}
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default RegisterPage;
