// src/pages/MyPage/VodPlayer.jsx
import React, { useEffect, useRef, useState } from "react";
import { useParams, useSearchParams } from "react-router-dom";
import {
  getVodInfo,
  updateAccessTime,
  updateCurrentTime,
} from "../../services/mypageService";

function VodPlayer() {
  const { vodId } = useParams();
  const [searchParams] = useSearchParams();
  const order_code = searchParams.get("order_code");

  const [vod, setVod] = useState(null);
  const [order, setOrder] = useState(null);
  const [mediaUrl, setMediaUrl] = useState("");
  const [loading, setLoading] = useState(true);

  const videoRef = useRef(null);
  const currentTime = useRef(0);
  const currentState = useRef("N");

  //    VOD  
  useEffect(() => {
    async function fetchVod() {
      try {
        const data = await getVodInfo(vodId, order_code);
        setVod(data.vod);
        setOrder(data.order);

        //  URL 
        const mediaServerUrl =
          data.media_server + "/" + data.media_folder + "/" + data.vod.Vod_file;
        setMediaUrl(mediaServerUrl);

        //   
        if (data.vodHistory && data.vodHistory.Current_time) {
          currentTime.current = data.vodHistory.Current_time;

          // lm_time  ( 60 )
          const lm_time = data.vod.Vod_time * 60 * 60 - 60;

          if (currentTime.current === 0 || currentTime.current > lm_time) {
            currentState.current = "N"; //   
          } else {
            currentState.current = "Y"; //  
          }
        }

        setLoading(false);

        // 1  current_time   ( )
        setTimeout(() => current_time(), 1000);
      } catch (error) {
        console.error("VOD   :", error);
        setLoading(false);
      }
    }

    fetchVod();
  }, [vodId, order_code]);

  // 30 access_time 
  useEffect(() => {
    if (!loading) {
      const timer = setInterval(() => access_time(), 30000);
      return () => clearInterval(timer);
    }
  }, [loading]);

  // 10  
  useEffect(() => {
    if (!loading) {
      const checkInterval = setInterval(() => {
        if (videoRef.current) {
          videoRef.current.pause();
          if (
            window.confirm(
              "    ? [10  ]\n\n   '' ."
            )
          ) {
            videoRef.current.play();
          }
        }
      }, 600000); // 10

      return () => clearInterval(checkInterval);
    }
  }, [loading]);

  //   
  useEffect(() => {
    const handleBeforeUnload = (event) => {
      event.preventDefault();
      currentTimeFunc();
    };

    window.addEventListener("beforeunload", handleBeforeUnload);
    return () => window.removeEventListener("beforeunload", handleBeforeUnload);
  }, [loading]);

  // 30   
  function access_time() {
    if (order && vod) {
      updateAccessTime(vod.No, order.Order_code);
    }
  }

  //  
  function current_time() {
    if (currentState.current === "Y") {
      if (
        window.confirm("  .\n\n ?")
      ) {
        seek(currentTime.current);
      } else {
        play();
        return false;
      }
    } else {
      play();
      return false;
    }
  }

  //  
  function seek(s) {
    if (videoRef.current) {
      videoRef.current.currentTime = s;
      videoRef.current.play();
      videoRef.current.volume = 0.25;
    }
  }

  //  
  function play() {
    if (videoRef.current) {
      videoRef.current.play();
      videoRef.current.volume = 0.25;
    }
  }

  //    
  function currentTimeFunc() {
    if (videoRef.current && order && vod) {
      const currentTime1 = videoRef.current.currentTime;
      updateCurrentTime(vod.No, order.Order_code, currentTime1);
    }
  }

  if (loading) {
    return <div> ...</div>;
  }

  if (!vod || !order) {
    return <div>VOD    .</div>;
  }

  return (
    <body
      style={{ margin: 0, padding: 0 }}
      onContextMenu={() => false}
      onSelectStart={() => false}
      onDragStart={() => false}
    >
      <form name="f1" id="f1" method="post">
        <input type="hidden" name="order_code" value={order.Order_code} />
        <input type="hidden" name="vod_no" value={vod.No} />
      </form>

      <table width="100%" border="0" cellSpacing="0" cellPadding="0">
        <tr>
          <td width="100%" align="center" valign="top" bgcolor="#000000">
            <video
              id="video1"
              ref={videoRef}
              controls
              autoPlay
              width="100%"
              height="100%"
              loop
              poster=""
              controlsList="nodownload"
              volume="0.5"
            >
              <source src={mediaUrl} type="video/mp4" />
                 .
            </video>

            <div style={{ padding: "10px" }}>
              <table
                border="0"
                cellSpacing="0"
                cellPadding="0"
                style={{ display: "fixed" }}
              >
                <tr>
                  <td
                    height="35"
                    style={{ padding: "0 0 0 10px", color: "#FFFFFF" }}
                  >
                    {vod.Vod_title}
                  </td>
                </tr>
              </table>
            </div>
          </td>
        </tr>
      </table>

      {/*   (    ) */}
      <iframe
        src=""
        name="MAFrame"
        id="MAFrame"
        width="0"
        height="0"
        scrolling="no"
        marginHeight="0"
        marginWidth="0"
        frameBorder="0"
      />
      <iframe
        src=""
        name="MAFrame2"
        id="MAFrame2"
        width="0"
        height="0"
        scrolling="no"
        marginHeight="0"
        marginWidth="0"
        frameBorder="0"
      />
    </body>
  );
}

export default VodPlayer;
