// src/context/AuthContext.js
import React, { createContext, useState, useContext, useEffect } from "react";
import axios from "axios";

//  Axios 
axios.defaults.withCredentials = true;

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [loading, setLoading] = useState(true);

  //      (  )
  const checkAuthStatus = async () => {
    try {
      const response = await axios.get("/api/Member/current-user");

      if (response.data.success && response.data.isAuthenticated) {
        setCurrentUser(response.data.user);
        setIsLoggedIn(true);
      } else {
        setCurrentUser(null);
        setIsLoggedIn(false);
      }
    } catch (error) {
      console.error("   :", error);

      //   : current-user  check-session 
      if (error.response && error.response.status === 401) {
        try {
          const sessionResponse = await axios.get("/api/Member/check-session");
          if (sessionResponse.data.isAuthenticated) {
            //       -    
            setIsLoggedIn(true);
            //        
            try {
              const userResponse = await axios.get("/api/Member/current-user");
              if (userResponse.data.success) {
                setCurrentUser(userResponse.data.user);
              }
            } catch (userError) {
              console.error("   :", userError);
            }
          } else {
            setCurrentUser(null);
            setIsLoggedIn(false);
          }
        } catch (sessionError) {
          console.error("  :", sessionError);
          setCurrentUser(null);
          setIsLoggedIn(false);
        }
      } else {
        setCurrentUser(null);
        setIsLoggedIn(false);
      }
    } finally {
      setLoading(false);
    }
  };

  //      
  useEffect(() => {
    checkAuthStatus();
  }, []);

  //  
  const login = (userData) => {
    setCurrentUser(userData);
    setIsLoggedIn(true);
  };

  //  
  const logout = async () => {
    try {
      const response = await axios.post("/api/Member/logout", {});
      if (response.data.success) {
        //     
        setCurrentUser(null);
        setIsLoggedIn(false);
      }
    } catch (error) {
      console.error(" :", error);
      //      
      // (      )
      setCurrentUser(null);
      setIsLoggedIn(false);
    }
  };

  //  
  const value = {
    currentUser,
    isLoggedIn,
    loading,
    login,
    logout,
    checkAuthStatus,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export const useAuth = () => {
  return useContext(AuthContext);
};

export default AuthContext;
