// src/pages/Member/LoginPage.jsx
import React, { useState, useEffect } from "react";
import { useNavigate, useLocation, Link } from "react-router-dom";
import Breadcrumbs from "../../components/Breadcrumbs";
import { useAuth } from "../../context/AuthContext";
import axios from "axios";

function LoginPage() {
  const [memberId, setMemberId] = useState("");
  const [passwd, setPasswd] = useState("");
  const [error, setError] = useState(""); //    
  const navigate = useNavigate();
  const location = useLocation();
  const { login, isLoggedIn } = useAuth();

  //     
  useEffect(() => {
    if (isLoggedIn) {
      navigate("/");
    }
  }, [isLoggedIn, navigate]);

  //  
  const handleLogin = async () => {
    if (!memberId.trim()) {
      setError("  .");
      return;
    }
    if (!passwd.trim()) {
      setError("  .");
      return;
    }

    setError("");

    try {
      //  LoginRequest    
      const response = await axios.post("/api/Member/login", {
        MemberId: memberId,
        Password: passwd,
      });

      //     AuthContext 
      if (response.data.success) {
        //    (   )
        login(response.data.user);

        //      
        const from = location.state?.from?.pathname || "/";
        navigate(from);
      } else {
        setError(response.data.message || " .");
      }
    } catch (error) {
      console.error(" :", error);

      //     
      if (error.response) {
        //    -     
        if (error.response.status === 401) {
          setError("   .");
        } else if (
          error.response.data.errors &&
          Array.isArray(error.response.data.errors)
        ) {
          //   errors   
          setError(error.response.data.errors.join(", "));
        } else {
          setError(error.response.data.message || "  .");
        }
      } else if (error.request) {
        //      
        setError("   .   .");
      } else {
        //     
        setError("   .");
      }
    }
  };

  //    
  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      handleLogin();
    }
  };

  //       
  useEffect(() => {
    const idInput = document.querySelector('input[type="text"]');
    if (idInput) {
      idInput.focus();
    }
  }, []);

  return (
    <>
      {/* ======= Breadcrumbs ======= */}
      <Breadcrumbs
        title=" "
        description="Est dolorum ut non facere possimus quibusdam eligendi voluptatem. 
        Quia id aut similique quia voluptas sit quaerat debitis. 
        Rerum omnis ipsam aperiam consequatur laboriosam nemo harum praesentium."
      />
      {/* ======= End Breadcrumbs ======= */}

      <section className="section section-shaped section-lg">
        <div className="container pt-lg-7">
          <div className="row justify-content-center">
            <div className="col-lg-5">
              <div className="card bg-secondary shadow border-0">
                <div className="card-header bg-white pb-5">
                  <div className="text-muted text-center mt-3">
                    <h2> </h2>
                  </div>
                </div>
                <div
                  className="card-body px-lg-5 py-lg-5"
                  style={{ background: "#c1c1c1" }}
                >
                  <div className="text-center text-muted mb-4">
                    <small></small>
                  </div>

                  {/*    */}
                  {error && (
                    <div className="alert alert-danger" role="alert">
                      {error}
                    </div>
                  )}

                  <div className="form-group mb-3">
                    <div className="input-group input-group-alternative">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-email-83"></i>
                        </span>
                      </div>
                      <input
                        type="text"
                        className="form-control"
                        placeholder=""
                        style={{
                          marginLeft: "5px",
                          borderTopLeftRadius: "0.3",
                          borderBottomLeftRadius: "0.3",
                        }}
                        value={memberId}
                        onChange={(e) => setMemberId(e.target.value)}
                        onKeyDown={handleKeyDown}
                      />
                    </div>
                  </div>
                  <div className="form-group focused">
                    <div className="input-group input-group-alternative">
                      <div className="input-group-prepend">
                        <span className="input-group-text">
                          <i className="ni ni-lock-circle-open"></i>
                        </span>
                      </div>
                      <input
                        type="password"
                        className="form-control"
                        placeholder=""
                        style={{
                          marginLeft: "5px",
                          borderTopLeftRadius: "0.3",
                          borderBottomLeftRadius: "0.3",
                        }}
                        value={passwd}
                        onChange={(e) => setPasswd(e.target.value)}
                        onKeyDown={handleKeyDown}
                      />
                    </div>
                  </div>
                  <div className="text-center">
                    <button
                      type="button"
                      className="btn btn-primary my-4"
                      style={{ background: "#5fcf80", borderColor: "#5fcf80" }}
                      onClick={handleLogin}
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
              <div className="row mt-3">
                <div className="col-6">
                  <Link to="/Member/Register" className="">
                    <small></small>
                  </Link>
                </div>
                <div className="col-6" style={{ textAlign: "right" }}>
                  <Link to="/Member/FindIdPw" className="">
                    <small>/ </small>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default LoginPage;
