// src/pages/Courses/Enrollment.jsx
import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getLectureDetail } from "../../services/courseService";
import Breadcrumbs from "../../components/Breadcrumbs";

function Enrollment() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [lecture, setLecture] = useState(null);
  const [payType, setPayType] = useState("bank");
  const [bankDepositor, setBankDepositor] = useState("");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchLecture() {
      try {
        const data = await getLectureDetail(id);
        setLecture(data);
      } catch (error) {
        console.error("   :", error);
      } finally {
        setLoading(false);
      }
    }

    fetchLecture();
  }, [id]);

  if (loading) {
    return <div>...</div>;
  }

  if (!lecture) {
    return <div>    .</div>;
  }

  const handlePayment = () => {
    if (payType === "credit") {
      alert("    .  .");
      return;
    }
    if (!bankDepositor) {
      alert(" .");
      return;
    }
    if (window.confirm("   ?")) {
      //     
      navigate("/courses");
    }
  };

  return (
    <>
      {/* Breadcrumbs */}
      <Breadcrumbs
        title=" "
        description="    ."
      ></Breadcrumbs>

      {/* Course Enrollment Section */}
      <section id="course-details" className="course-details">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <img
                src={`/files/Lecture/${lecture.img1}`}
                className="img-fluid"
                alt=" "
              />
              <h3>{lecture.name}</h3>
            </div>
          </div>
        </div>
      </section>

      <section id="cource-details-tabs" className="cource-details-tabs">
        <div className="container">
          <div className="row">
            <table className="table table-hover table-bordered">
              <thead>
                <tr className="text-center">
                  <th></th>
                  <th> </th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="text-center">
                    {lecture.lectureTeachers
                      .map((t) => t.jcTeacher.teacherName)
                      .join(", ")}
                  </td>
                  <td className="text-center">
                    {lecture.lecStart} ~ {lecture.lecEnd}
                  </td>
                  <td className="text-center">
                    {lecture.price > 0 ? (
                      `${lecture.price.toLocaleString()}`
                    ) : (
                      <span style={{ color: "#d2a573" }}></span>
                    )}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>

          <div className="row mt-2">
            <table className="table table-hover table-bordered">
              <tbody>
                <tr>
                  <th></th>
                  <td className="text-center">
                    <input
                      type="radio"
                      id="pay_type1"
                      name="pay_type"
                      value="bank"
                      checked={payType === "bank"}
                      onChange={() => setPayType("bank")}
                    />
                    <label htmlFor="pay_type1"></label>
                    &nbsp;&nbsp;
                    <input
                      type="radio"
                      id="pay_type2"
                      name="pay_type"
                      value="credit"
                      checked={payType === "credit"}
                      onChange={() => setPayType("credit")}
                    />
                    <label htmlFor="pay_type2"></label>
                  </td>
                </tr>

                {payType === "bank" && (
                  <>
                    <tr>
                      <th></th>
                      <td className="text-center">
                        <select>
                          <option> 245-23-2222222 ()</option>
                          <option> 123-43-5656565 ()</option>
                          <option> 2245-11-85758546 ()</option>
                        </select>
                      </td>
                    </tr>
                    <tr>
                      <th></th>
                      <td className="text-center">
                        <input
                          type="text"
                          className="form-control w-80"
                          value={bankDepositor}
                          onChange={(e) => setBankDepositor(e.target.value)}
                        />
                      </td>
                    </tr>
                  </>
                )}
              </tbody>
            </table>

            <div className="text-center">
              <button
                className="btn btn-secondary mt-2"
                onClick={handlePayment}
              >
                
              </button>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Enrollment;
